import Swiper, { Navigation, Pagination, Autoplay, EffectFade, Parallax} from 'swiper';

const delay = 1500;


const desktop = {
	modules: [Navigation, Pagination, Autoplay, EffectFade, Parallax],
	rebuildOnUpdate: true,
	observer: true,
    observeParents: true,
  	loop: true,
	slidersPerView: 1,
	speed: delay,
	parallax: true,
	fadeEffect: {
		crossFade: true
	},
	effect: 'fade',
	
	autoplay: {
		delay: 2000,
		disableOnInteraction: false,
	},
	navigation: {
		nextEl: '.slider-button-next',
		prevEl: '.slider-button-prev',
	},
	pagination: {
		el: '.slider-pagination',
		type: 'fraction',
		formatFractionCurrent: addZero,
		formatFractionTotal: addZero
	},
}
const mobile = {
	modules: [ Pagination, Autoplay, EffectFade, Parallax],
	rebuildOnUpdate: true,
	observer: true,
    observeParents: true,
  	loop: true,
	slidersPerView: 1,
	speed: delay,
	parallax: true,
	fadeEffect: {
		crossFade: true
	},
	effect: 'fade',
	
	autoplay: {
		delay: 2000,
		disableOnInteraction: false,
	},
	pagination: {
		el: '.slider-pagination',
		type: 'bullets'
	},
}

let bulletBarLine = document.querySelector('.slider-pagination-bar__line')
let swiper;

const barActive = () => {
	bulletBarLine.style=`width:0`
	setTimeout(()=>{
		bulletBarLine.style=`width:100%;transition: ${delay}ms;`
	}, 0)
}

if ( window.innerWidth > 992) {
	swiper = new Swiper('.swiper', desktop);
	bulletBarLine.style=`width:100%;transition: ${delay}ms;`
}else if ( window.innerWidth < 991) {
	swiper = new Swiper('.swiper', mobile);
}



window.addEventListener('resize', function () {
	if ( window.innerWidth > 992) {
		swiper.destroy();
		swiper = new Swiper('.swiper', desktop);
		barActive()
		swiper.on('slideChange', barActive);
	}else if ( window.innerWidth < 991) {
		swiper.destroy();
		swiper = new Swiper('.swiper', mobile);
	}
});

function addZero(num){
	return (num > 9) ? num : '0' + num;
}

swiper.on('slideChange', barActive);



